import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'

import Home from '@/pages/default/Home'
import Shop from '@/pages/default/Shop'
import User from '@/pages/default/User'
import Login from '@/pages/default/Login'
import City from '@/pages/default/City'
import MyCarsr from '@/pages/default/MyCarsr'
import BindingPlates from '@/pages/default/BindingPlates'
import VehicleModel from '@/pages/default/VehicleModel'
import Coupons from '@/pages/default/Coupons'
import YearCard from '@/pages/default/YearCard'
import CouponRedemption from '@/pages/default/CouponRedemption'
import Order from '@/pages/default/Order'
import OrderDetails from '@/pages/default/OrderDetails'
import CardDetails from '@/pages/default/CardDetails'
import ModifyPlate from '@/pages/default/ModifyPlate'

Vue.use(Router)

const routes=[
    {path: '/',name:'洗车商家',component: Home},
    {path: '/Home',name: '洗车商家',component: Home},
    {path: '/Shop',name: '商家详情',component: Shop},
    {path: '/Login',name: '登录',component: Login},
    {path: '/City',name: '选择城市',component: City},
    {path: '/User',name: '个的中心',component: User,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/MyCarsr',name: '我的爱车',component: MyCarsr,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
	     {path: '/Order',name: '我的订单',component: Order,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
	      {path: '/OrderDetails',name: '订单详情',component: OrderDetails,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/BindingPlates',name: '绑定车牌',component: BindingPlates,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/VehicleModel',name: '选择车型',component: VehicleModel,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/Coupons',name: '我的优惠券',component: Coupons,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
	    {path: '/ModifyPlate',name: '修改绑定车牌',component: ModifyPlate,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/YearCard',name: '我的洗车卡',component: YearCard,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
	     {path: '/CardDetails',name: '车卡详情',component: CardDetails,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }},
    {path: '/CouponRedemption',name: '领取洗车券',component: CouponRedemption,
	    meta:{
	    	loginIng:true //添加该字段，表示进入这个路由是需要登录的
	    }}
  ]

export default new Router({mode:'history',routes});






