<template>
   <!--选择车型-->
  <div class="VehicleModel">
  	<div class="u-search">
  		<mt-search v-model="value" cancel-text="取消" placeholder="搜索" :result="filterResult"></mt-search>
  	</div>
           <div class="page-indexlist">
			    <div class="page-indexlist-wrapper">
			      <mt-index-list>
			        <mt-index-section v-for="(item,index) in alphabet" :index="item.initial">
			          <mt-cell v-for="(cell,index) in item.cells" :title="cell" v-on:click.native="LookOver($event,index)"></mt-cell>
			        </mt-index-section>
			      </mt-index-list>
			    </div>
			  </div>
			  
			    <mt-popup v-model="popup" position="right" class="g-popup" :modal="false">
			           <span class="select-vehicle-title" ref="vehiclenmae">别克别克别克别克别克别克别克别克别克</span>
			           <span class="select-vehicle-ico"><i class="iconfont" v-on:click="popup=false">&#xe735;</i></span>
			           <div class="select-vehicle-con">
			             <ul>
			             	<li v-for="item in CarBrandInfos">{{item.Name}}</li>
			             	<li v-for="items in CarBrandInfos.ChildBrandInfo">
			             		{{items.Name}}
			             	</li>
			             </ul>
			           </div>
			    </mt-popup>
  </div>
</template>

<script>
import { Search } from 'mint-ui';
import { IndexList, IndexSection } from 'mint-ui';
import Public from './../../router/public.js';
import router from './../../router';
export default {
  name: 'VehicleModel',
  data () {
    return {
     value: '',
     popup: false,
     defaultResult:['Apple','Banana','Orange','Durian','Lemon','Peach','Cherry','Berry','Core','Fig','Haw','Melon','Plum','Pear','Peanut','Other','Aaron', 'Alden', 'Austin', 'Baldwin', 'Braden', 'Carl', 'Chandler', 'Clyde', 'David', 'Edgar', 'Elton', 'Floyd', 'Freeman', 'Gavin', 'Hector', 'Henry', 'Ian', 'Jason', 'Joshua', 'Kane', 'Lambert', 'Matthew', 'Morgan', 'Neville', 'Oliver', 'Oscar', 'Perry', 'Quinn', 'Ramsey', 'Scott', 'Seth', 'Spencer', 'Timothy', 'Todd', 'Trevor', 'Udolf', 'Victor', 'Vincent', 'Walton', 'Willis', 'Xavier', 'Yvonne', 'Zack', 'Zane'],
     alphabet: [],
     CarBrandInfos:[]
    }
  },
  computed:{ 
  	filterResult() {
      return this.defaultResult.filter(value => new RegExp(this.value, 'i').test(value));
    }
  },
  created(){
  	   'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.split('').forEach(initial => {
        let cells = this.defaultResult.filter(name => name[0] === initial);
        this.alphabet.push({initial,cells});
      });
  },
  methods:{
  	 LookOver:function(event,index){
  	 var nmae=event.currentTarget.innerHTML;
  	 console.log(nmae)
  	 this.popup=true;
  	 var $this=this;
  	 Public.__ajax(this.$store.state.apiUrl+'brandtype.json',{index:index},function(data){
  	  	  $this.CarBrandInfos=data;
  	  })
  	 }
  },
  mounted:function(){
  	  let lef=this;
  	  Public.__ajax(this.$store.state.apiUrl+'brandtype.json',{},function(data){
  	  	 //console.log(JSON.stringify(data))
  	  	 //lef.defaultResult=data.NameAlphabet
  	  })
  }
}
</script>



