  	 	  }
  	 	  if(!carNo || carNo == '') {
	  	 	  Toast('请输入车牌号！');
	  	 	  return false;
        }
  	 	  //根据车类型来拼接车牌
        switch (radionam) {
            case "普":
                _carNo = puId + numId + carNo;
