                    'carNo': _carNo,
                    'carBrandID':carno_CarBrandID,
                    'carNoType': carBindInfo.carNoType,
                    'remark': '',
                    'status': 1
                };
  	 	  
  	 }
  },
  watch: { //当数据改变时自动引发事件
  	isValue(curVal,oldVal){
  		 console.log(curVal,oldVal)
  		//根据车类型来拼接车牌
        switch (curVal) {
