<template>
  <div class="home">
  	<ul class="navigate">
        		  <li><router-link to="a.html"><p><i class="iconfont" style="color: #3cb7f3;">&#xe634;</i></p><p>洗车</p></router-link></li>
        	 		<li><router-link to="a.html"><p><i class="iconfont" style="color: #ffc528;">&#xe637;</i></p><p>补漆</p></router-link></li>
        	 		<li><router-link to="a.html"><p><i class="iconfont" style="color: #966dfe;">&#xe638;</i></p><p>维修</p></router-link></li>
        	 		<li><router-link to="a.html"><p><i class="iconfont" style="color: #6fd36a;">&#xe639;</i></p><p>保养</p></router-link></li>
        	 </ul>
  	  <loadmore :top-method="loadTop" :bottom-method="loadBottom"  :auto-fill="false" :bottom-all-loaded="allLoaded" ref="top">
	    <ul class="m-tab">
	    	 <li v-for="(item,index) in items" v-on:click="Shop(item.ComID,index)">
	    	 	  <div class="tab-left">
	    	 	  <img v-lazy="item.PicUrl" :data-srcset="item.PicUrl" class="lazy-img-fadein object" />
	    	 	  </div>
	    	 	  <div class="tab-media">
	    	 	  	<h3>{{ item.ShopName }}</h3>
	    	 	  	<p><i class="iconfont">&#xe6ba;</i>地址：{{ item.Address }}</p>
	    	 	  	<p><i class="iconfont">&#xe601;</i>电话：{{ item.LinkPhone }}</p>
	    	 	  	<p class="service-items"><a>保养</a><a>补漆</a><a>维修</a><a>洗车</a></p>
	    	 	  </div>
	    	 	  <div class="tab-right">{{ item.Distance }}</div>
	    	 </li>
	    </ul>
   </loadmore>
   <!--没有更多啦提示-->
   <div v-if="allLoaded" class="exactly">
   	     <i class="iconfont">&#xe64a;</i>
   	     <p>没有更多啦!</p>
   </div>
  </div>
</template>
<script>
import {Toast,Loadmore } from 'mint-ui'; //上拉/下拉刷新
import Public from './../../router/public.js';
import router from './../../router';

export default {
  data () {
    return {
    	Page:1,
    	list: [],
      allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
      items:'',
			getShopData(Page){ //请求数据
				  	let $this=this
		         Public.__ajax(this.$store.state.apiUrl+'home.json',{Page:Page},function(data){
		          	$this.items=data
		          	if(Page>1){
		          		$this.items =$this.items.concat(data);
		          		if(Page>3){
		          			 data.length=0
		          			if(data.length==0){
		          			 $this.allLoaded = true;// 若数据已全部获取完毕
		          			 $this.$refs.loadmore.onBottomLoaded();      			 
		          		 }
		          		}
		          	
		          	}
		         })
				  }
    }
  },
  components: { //组件 
    'loadmore': Loadmore  //组件名
  },
  methods:{
  	 Shop:function(id,index){
        //带查询参数，变成 /shop?id=id
        router.push({path: 'shop', query: { id:id}})
  	 },
  	 loadTop(id) { //下拉刷新
  	 	console.log("下拉刷新~~~！")
  	 	//下拉加载  
       this.getShopData(this.Page) 
       this.$refs.top.onTopLoaded(id);// 固定方法，查询完要调用一次，用于重新定位 
    },
    loadBottom(id) { //上拉加载更多数据
       this.Page += 1;
       console.log("上拉加载更多数据"+this.Page);
       this.getShopData(this.Page)
       //this.$refs.loadmore.onBottomLoaded();
    }
  },
  created() { //写入第一加载数据获取数据
  	 this.getShopData(this.Page)
  },
  mounted:function(){ //加载完成后执行
  	 //this.getShopData(this.Page) //写入第一加载数据获取数据
  	 
  }
}
	
</script>




