<template>
  <div class="YearCard">
        <div class="car-hiet">
        	<i class="iconfont">&#xe681;</i>点击卡片领取洗车券
        </div>
       <a class="g-wash-car" v-on:click="addGetAcoupon">
       	<div class="title">
       		<h5>任务程序测试</h5> 
       		<p>NO. 3110100000000139</p> 
       		<p>使用地区：贵州省</p>
       	</div> 
       		<ul class="f-wash-list">
       			<li><h6>剩余次数</h6>
       				<p><span class="z-num">12</span> 次</p></li> 
       				<li><h6>使用次数</h6> <p><span class="z-num">0</span> 次</p></li> 
       				<li><h6>已过期次数</h6> <p><span class="z-num">0</span> 次</p></li>
       		</ul> <div class="u-wash-bottom">
       			<span class="z-time-left"><em>生效时间：</em>
                        2017-04-27</span> <span class="z-time-right"><em>截止时间：</em>
                       2017-10-31</span></div>
       </a>
       
       <div  class="wsacarbtn">
       	 <img src="assets/addrwasg.png" width="120px" v-on:click="addYearCard">
       </div>
       
       
         <!--我知提示-->
         <div class="know-carcard" v-if="knowCarcard">
                <img src="assets/washthecar_03.png"/>
                <img src="assets/washthecar_01.png" class="hand"/>
                <img src="assets/washthecar_08.png" class="know" v-on:click="removKnow"/>
            </div>
       
       
		  <!--添加洗车卡弹框信息-->
		  <modal v-if="showModal"  v-on:close="showModal=false">
		    <h3 slot="header">输入激活码</h3>
		    <div slot="body" class="card-active">
		    	 <input type="tel" maxlength="8" placeholder="请输入您的洗车卡激活码"  v-on:input ="addCard($event)"/> 
		    	 <p><i class="iconfont">&#xe681;</i>{{message}}</p>
		    </div>
		    <div slot="footer"></div>
		  </modal>
	  
	   
	    <!--领取洗车劵-->
    <modal v-if="GetAcoupon"  v-on:close="GetAcoupon=false">
		    <h3 slot="header">输入激活码</h3>
		    <div slot="body">
		    	 <div class="m-wash-car-alert">
                         <p class="m-hint">您的此张洗车卡，每月只能领取<span class="blue">1</span>张券<br />
                         本月<span class="blue">已领完</span>，请下个月再来。</p>
                       
                     </div>
                     <div class="steps-title">
                     	<img src="assets/hang.jpg" class="object" /></div>
                       <ul class="steps">
                           <li class="active">激活洗车卡</li>
                           <li>绑定车牌</li>
                           <li>领取洗车劵</li>
                       </ul>
		    </div>
		    <div slot="footer">
		    	   <button class="modal-default-button modal-default-ticket" v-on:click="">查看我的洗车劵</button>
		    	   <button class="modal-default-button modal-default-ticket" v-on:click="">领取优惠劵</button>
		    </div>
		  </modal>

  </div>
</template>

<script>
import { Cell } from 'mint-ui';
import modal from '@/components/modal'
export default {
  name: 'YearCard',
  data () {
    return {
    	showModal:false,
    	GetAcoupon:false,
    	knowCarcard:false,
    	message:'请输入您的洗车卡激活码'
    }
  },
  components:{
  	'modal':modal
  },
  methods:{
  	 addGetAcoupon:function(){
  	 	  this.GetAcoupon=true
  	 },
  	 addYearCard:function(){ //点击添加卡
  	 	 this.showModal=true
  	 },
  	 addCard:function(event){ //激活卡成功
  	 	   var event = event.currentTarget;
         if(event.value.length>=8){
         	 this.message="激活成功！"
         	 this.showModal=false;
         	 this.knowCarcard=true;
         }
  	 },
     removKnow:function(){ //点击我知道
     	  this.knowCarcard=false
       }
  }
}
</script>



