<template>
  <div class="Login">
  	<div class="g-login">
  		 <img src="assets/login_top.png">
  	</div>
  	<div class="m-login">
  	<div class="page-part">
  		<mt-field label=" " placeholder="请输入您的手机"  ref="username" v-model="username" type="tel" :attr="{maxlength:11}"><i class="iconfont">&#xe684;</i></mt-field>
	    <mt-field label=" " placeholder="请输入您的验证码" ref="codeValue" type="tel" :attr="{maxlength:6}" ><i class="iconfont">&#xe625;</i> </mt-field>
	    <span v-if="sendMsgDisabled" class="code">{{time+'秒后获取'}}</span>
      <span v-if="!sendMsgDisabled" class="code code-get"  @click="send">免费获取验证码</span>
  	</div>
    <mt-button type="primary" class="btn-login" v-on:click="registerClick">登录</mt-button>
  	</div>

     

  </div>
</template>

<script>
import { Field ,Button,Toast} from 'mint-ui';
import axios from 'axios';
export default {
  name: 'login',
  data () {
    return {
       time: 60, // 发送验证码倒计时
       username:'',
       codeValue:'',
       reg:/^(13[0-9]|14[0-9]|15[0-9]|17[0-9]|18[0-9])\d{8}$/,//手机验证码
       sendMsgDisabled: false
    }
  },
  methods:{
  	  registerClick:function(){ //提交登录
  	  	 let username=this.$refs.username.value;
  	  	 let codeValue=this.$refs.username.value;
  	  	if(username.length<11){
  	  	 	 Toast('请输入手机号！')
  	  	 	 return;
  	  	 }
  	   if(!this.reg.test(username)) {
              Toast('手机号格式不正确！')
              return;
           }
  	  	return false;
  	  	//window.location.href='/#/User'
  	  	axios.post('/user', {
				    firstName: 'Fred',
				    lastName: 'Flintstone'
				  })
				  .then(function (response) {
				    console.log(response);
				  })
				  .catch(function (error) {
				    console.log(error);
				  });
  	  },
     send:function(){
     	 let username=this.$refs.username.value;
       	if(username.length<11){
  	  	 	 Toast('请输入手机号！')
  	  	 	 return;
  	  	 }
  	   if(!this.reg.test(username)) {
              Toast('手机号格式不正确！')
              return;
           }
     	  
        let me = this;
        me.sendMsgDisabled = true;
        let interval = window.setInterval(function() {
          if ((me.time--) <= 0) {
            me.time = 60;
            me.sendMsgDisabled = false;
            window.clearInterval(interval);
          }
        }, 1000);
        //ajax 获取验证码
        	console.log(this.$store.state.token)
  	     	this.$store.state.token="我改"
          console.log(this.$store.state.token)
      }
  }
}
</script>



