Vue.config.productionTip = false //设置为 false以阻止 vue 在启动时生成生产提示。


// 如果在模块化构建系统中，请确保在开头调用了 Vue.use(Vuex)
//this.$store.state.token  子组件能通过 this.$store 访问到
const store = new Vuex.Store({
  state: {
    token:'18285533808',//用户名cokie
    apiUrl:'http://localhost:8080/static/json/', //请求地址http://192.168.0.144:9123
    imgUrl:'http://192.168.0.56:8139/'     //图片地址
  },
   mutations: {
    token(state) {
      // 变更状态
      state.token
    }
  }
})

Public.setCookie()

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,// 把 store 对象提供给 “store” 选项，这可以把 store 的实例注入所有的子组件
  router,/*使用路由*/
  template: '<App/>',/*告知页面这个组件用这样的标签来包裹着,并且使用它*/
  components: {
  	"App":App /*告知当前页面想使用App这个组件*/
  }
})



//第一步：拦截器定义完路由后，我们主要是利用vue-router提供的钩子函数beforeEach()对路由进行判断。
router.beforeEach((to, from, next) => {
	console.log(store.state.token)
    if (to.meta.loginIng) {  // 判断该路由是否需要登录权限
        if (store.state.token) {  // store.state.token通过vuex state获取当前的token是否存在
            next();
        }
        else {
            next({
                path: '/Login',
                query: {redirect:"failure"}  //将跳转的路由path作为参数，登录成功后跳转到该路由
            })
        }
    }
    else {
        next();
    }
})

//第二步http request 拦截器
axios.interceptors.request.use(
    config => {
        if (store.state.token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
            config.headers.Authorization = `token ${store.state.token}`;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    });

// http response 拦截器
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    // 返回 401 清除token信息并跳转到登录页面
                    store.commit(types.LOGOUT);
                    router.replace({
                        path: 'login',
                        query: {redirect: router.currentRoute.fullPath}
                    })
            }
        }
        return Promise.reject(error.response.data)   // 返回接口返回的错误信息
    });


