<template>
  <div class="Shop">
      <div class="m-top">
             <img  v-bind:src="items.PicUrl">
            <div class="img-zindex"></div>
       </div>
       <div class="u-addres">
       	<h4>{{items.ComName}}</h4> 
       	<div class="z-addres">
       		<i class="iconfont z-map-icon">&#xe6ba;</i>
       		{{items.Address}}</div>
       		<div class="u-map-phone">
       			<a v-bind:href="'tel:'+items.LinkPhone" class="z-map z-map-phone">拨打电话<i class="iconfont">&#xe604;</i></a> 
       			<span class="z-map" v-on:click="sheetVisible = true">一键导航<i class="iconfont">&#xe696;</i></span></div></div>
           <div class="details-services-title">服务项目</div>
           <ul>
		   <!--服务项目-->
			 		<li class="services-list">
			 			<div class="title-box">
			 			<div class="title"><i class="iconfont">&#xe63c;</i>洗车</div>
			 			<div class="triangle"></div>
			 			</div>
			 		  <ul>
			 		  	<li v-for="(item,index) in items.Services" v-on:click="SelectTag(index)">
			 		  			<div class="services-left">{{item.ServiceName}}</div>
			 		  			<div class="services-body"><del>￥{{item.Price}}</del><span class="price">￥{{item.Price}}</span></div>
			 		  			<div class="services-right"><i class="iconfont">&#xe735;</i></div>
			 		    </li>  
	 		     </ul>
	 		   </li>
			<!--服务项目 ned-->
			</ul>
           <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
           
           <!--弹出先择卡片-->
           <mt-popup v-model="popup" position="right" class="select-conter" :modal="false">
			           <div class="select-conter-title"><i class="iconfont" v-on:click="popup=false">&#xe66e;</i>选择卡片</div>
			            <div class="card-select-list">
			            	<mt-cell title="免费洗车" icon="more">
			            		  <i class="iconfont" slot="icon">&#xe653;</i>
			            		  <mt-checklist icon="more" align="right" class="page-part" v-model="value1" :options="options1">
			            	    </mt-checklist>
			            	</mt-cell>
			            	
			            </div>
	          	  <div class="card-select-explain">
	          	  	<h6>服务说明：</h6>
	          	  	<p>在车友助理指定签约商户使用免费洗车券可享受免费全车型普洗一次（货车、客车等特殊车型除外）。</p>
	          	  </div>
			    </mt-popup>
			    
	
  </div>
</template>

<script>
import { Actionsheet,Cell,Checklist } from 'mint-ui';
import Public from './../../router/public.js';
import router from './../../router';
export default {
  components: {
    'actionsheet': Actionsheet
  },
  data () {
    return {
    	value1:[],
      sheetVisible: false,
      actions: [],
      popup:false,
      items:[]
    }
  },
  methods:{
  	takePhoto:function() {
    console.log('taking photo');
    },
	  openAlbum:function(){
	      console.log('opening album');
	  },
	  SelectTag:function(){
	  	 this.popup=true;
	  }
  },
  created(){
  	 this.options1 = ['券号：6873534890'];
  		var lef=this;
      Public.__ajax(this.$store.state.apiUrl+'shop.json',{},function(data){
    	  lef.items = data;
    })
    
  },
  mounted:function(){
  	  this.actions = [{
      name: '高德地图',
      method: this.takePhoto
    }, {
      name: '百度地图',
      method: this.openAlbum
    },{
    	name:'腾讯地图',
    	method: this.openAlbum
    }];
  }
}
</script>



