<template>
  <div  class="OrderDetails">
    		<div  class="order-tab mb0">
					 <div class="order-body">
					   <div class="order-left">
					   	<img v-bind:src="items.PicUrl">
					   </div>
					   <div class="order-right">
					   	<h3>{{items.ComName}}</h3>
					     <p class="addres"><i class="iconfont">&#xe6ba;</i>{{items.Address}}</p>
					   </div>
					 </div>
					</div>
					<div class="u-map-phone order-phone">
						<a href="'tel:'+items.LinkPhone" class="z-map z-map-phone">拨打电话<i class="iconfont"></i></a>
						<span class="z-map">一键导航<i class="iconfont"></i></span>
					</div>
					
				<div class="order-detalis-list">
					 	<mt-cell title="消费方式:" value="保养卡">
						  <i slot="icon" class="iconfont">&#xe63f;</i>
						</mt-cell>
				</div>	
		
  </div>
</template>

<script>
import {Toast,Loadmore } from 'mint-ui'; //上拉/下拉刷新
import Public from './../../router/public.js';
import router from './../../router';
export default {
  data () {
    return {
      items:[]
    }
  },
  methods:{
  },
   created() { //写入第一加载数据获取数据
  },
  mounted:function(){ //加载完成后执行
  	var lef=this;
    Public.__ajax(this.$store.state.apiUrl+'orderDetails.json',{},function(data){
    	  lef.items = data;
    })
  }
}
</script>





