<template>
  <div class="MyCarsr">
      <div class="my-carsr" v-cloak v-for="(item,index) in items">
      	<div class="my-left">
      		<h4 class="blue">{{item.CarNo}}</h4> 
      		<p>{{item.CarBrandName}}</p></div>
      		<div class="my-right">
      			<img src="assets/xiaoche.jpg"></div>
      			<div class="my-bottom"><span>绑定时间：{{item.BindTime.substring(0,10)}} </span> 
      			<span v-if="new Date(item.UpTime.replace(/\s/, 'T'))>new Date()" class="z-my-bottom-right">下次修改时间：{{item.UpTime.substring(0,10)}}</span>
      			<span v-else v-on:click="edit(item.ID,index)" class="z-my-bottom-right blue">点击修改车牌</span> 
      			</div>
      </div>
      
      <div class="add-plate-number" v-on:click="add()">
      	<p class="carno-number"><img src="assets/attrcar.png" class="object"></p>
      	添加绑定车牌
      </div>
      
                <div class="bund-hit bund-hit-list"><i class="iconfont jingtanhao1"></i>
                    温馨提示：绑定的车牌六个月后才能进行车牌修改。
                </div>
  </div>
</template>

<script>
import Public from './../../router/public.js';
import router from './../../router';
export default {
  data () {
    return {
      items:[]
    }
  },
  methods:{
  	add:function(){ //填加绑定车牌
  		 //带查询参数，变成 /shop?id=id
        router.push({path: 'BindingPlates', query:{ id:'1'}})
  	},
  	edit: function (id, i){
  		 router.push({path: 'BindingPlates', query:{ id:'1'}})
  	}
  },
  created() {
  	
  },
  mounted:function(){ //加载完成后执行
  	  var lef=this;
        Public.__ajax(this.$store.state.apiUrl+'mycarsr.json',{},function(data){
    	   lef.items = data;
      })
  }
}
</script>



